.portPieces{
  text-align: center;
}

.gallery {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  width: 80%;
  margin-left: 10%
}

.portfolioPiece {
  position: relative;
  display: block;
  width: 30%;
  height: 200px;
  margin: 0 auto 2rem;
  background-size: cover;
  border-radius: 3px;
  -webkit-box-shadow: 0px 25px 50px rgba(0, 0, 0, 0.5);
          box-shadow: 0px 25px 50px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  -webkit-transition: all .4s ease;
  -o-transition: all .4s ease;
  transition: all .4s ease;
  background-position: center top;
  background-size: cover;
}

/*for tablet*/
@media (min-width: 768px) and (max-width: 991px){
  .portfolioPiece {
    width: 500px;
    margin-left: 5%;
  }
}

/*for mobile*/
@media (max-width: 767px){
  .portfolioPiece {
    width: 85%;
  }
}

.portLink {
  position: relative;
  display: block;
  width: 80%;
  top: 200px;
  padding: 10px;
  margin: 0 auto;
  color: white;
  letter-spacing: 3px;
  text-align: center;
  border: 2px solid white;
  border-radius: 3px;
  font-size: 1em;
  -o-transition: all 0.3s cubic-bezier(0.3, 0, 0, 1.3); /*opera*/ /*mozilla*/
  -webkit-transition: all 0.3s cubic-bezier(0.3, 0, 0, 1.3); /*safari*/
  transition: all 0.3s cubic-bezier(0.3, 0, 0, 1.3);
}

.portLink:hover {
  background-color: white;
  color: #241A17;
}

.portOverlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 10;
}

.portOverlay:after {
  content: '';
  width: 100%;
  border-radius: 10px;
  height: 100%;
  background-color: #241A17;
  opacity: 0;
  position: absolute;
  top: 0;
  z-index: -10;
  left: 0;
  -o-transition: all .3s ease; /*opera*/ /*mozilla*/
  -webkit-transition: all .3s ease; /*safari*/
  transition: all .3s ease;
}

.portTitle {
  position: relative;
  top: -200px;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 100;
  color: white;
  text-align: center;
  letter-spacing: 5px;
  -o-transition: all 0.3s cubic-bezier(0.3, 0, 0, 1.3); /*opera*/ /*mozilla*/
  -webkit-transition: all 0.3s cubic-bezier(0.3, 0, 0, 1.3); /*safari*/
  transition: all 0.3s cubic-bezier(0.3, 0, 0, 1.3);
}

.portDescription {
  position: relative;
  top: -200px;
  margin-bottom: 1.5rem;
  font-size: 1rem;
  font-weight: 100;
  color: white;
  text-align: center;
  -o-transition: all 0.3s cubic-bezier(0.3, 0, 0, 1.3); /*opera*/ /*mozilla*/
  -webkit-transition: all 0.3s cubic-bezier(0.3, 0, 0, 1.3); /*safari*/
  transition: all 0.3s cubic-bezier(0.3, 0, 0, 1.3);
}

.portfolioPiece:hover .portTitle {
  top: 0;
}

.portfolioPiece:hover .portDescription {
  top: 0;
}

.portfolioPiece:hover .portLink {
  top: 0;
}
.portfolioPiece:hover .portOverlay:after {
  opacity: .75;
}



