.navBar{
  background-color: #241A17;
  color: #eee;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
  position: fixed;
  z-index: 11;
}

h3 {
  font-family: 'Euphoria Script', cursive;
  font-size: 30px;
  display: block;
  -webkit-margin-before: 0.35em;
  -webkit-margin-after: 0.35em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
font-weight: bold;
}

.linksContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-right: 10px;
}

.navLink {
  color: #eee;
  margin-right: 25px
}

.brand {
  margin-top: 5px;
}

.scrolled{
  font-weight: bolder;
}



