.about{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
}

.bio {
  width: 50%;
  margin: 5%;
  text-align: center;
}

.mySkills {
  width: 30%;
  margin: 5%;
  text-align: center;
}


/*for mobile*/
@media (max-width: 767px){

  #about{
      width: 80%;
  }
  .mySkills {
    width: 80%;
  }
}

.blurb {
  font-size: 18px;
}

p {
    font-size: 24px;
}



