

.iconContainer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 65px;
}

.lilDot  {
  height: 0px;
  width: 0px;
  border-radius: 50%;
  background-color: #FDE36D;
  opacity: 0;
  z-index: -1;
  position: absolute;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
}

.iconContainer:hover > .lilDot {
  height: 60px;
  width: 60px;
  opacity: 0.85;
}

.footIcon {
  height: 35px;
  width: 45px;
  margin: 20px;
}



